import { ScatterChart } from "../ui";

export const createScatterChart = () => {
  const wrapper = document.createElement('div');
  const sc = new ScatterChart(wrapper, {
    axis: {
      x: {
        min: 1671684304000,
        max: 1671687904000,
        tick: {
          count: 5,
          // format: (value) => format(value, 'HH:mm:ss'), 
        }
      },
      y: {
        min: 0,
        max: 10000,
        tick: {
          count: 9,
          format: (value) => value.toLocaleString(),
        }
      }
    },
    data: [
      {
        type: 'success',
        color: 'green',
        priority: 11,
      },
      {
        type: 'fail',
        color: 'red',
        priority: 1,
      },
    ],
    legend: {
      formatLabel: (label) => label.toUpperCase(),
      formatValue: (value) => value.toLocaleString(),
    },
  });
  return wrapper;
}